import TopNav from '@/components/TopNav.vue';

@Component({
  components: {
    TopNav
  }
})
export default class Login extends Vue {
  private tableData = [
    {
      date: '2016-05-03',
      name: '',
      province: '',
      city: '',
      address: ' 1518 ',
      zip: 200333
    },
    {
      date: '2016-05-02',
      name: '',
      province: '',
      city: '',
      address: ' 1518 ',
      zip: 200333
    },
    {
      date: '2016-05-04',
      name: '',
      province: '',
      city: '',
      address: ' 1518 ',
      zip: 200333
    },
    {
      date: '2016-05-01',
      name: '',
      province: '',
      city: '',
      address: ' 1518 ',
      zip: 200333
    }
  ];

  private handleClick(rows: [{}], type: string, index: number) {
    if (type === 'del') {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          rows.splice(index, 1);
          this.$message({
            type: 'success',
            message: '!'
          });
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
    } else if (type === 'edit') {
      this.$message({
        type: 'info',
        message: ''
      });
      console.log('edit ', rows);
    } else if (type === 'detail') {
      this.$message({
        type: 'info',
        message: ''
      });
      console.log('detail ', rows);
    }
  }
}



