export interface refundOrderPublicInfo {
  treasureId: string;
  keyword1: string;
  keywordRate1: string;
  keyword2: string;
  keywordRate2: string;
  keyword3: string;
  keywordRate3: string;
  executeTime: string;
  mainPictureUrl: string;
  verticalPictureUrl: string;
  time_range: string;
  require: string;
}
export interface freeOrderPublicInfo {
  title: string;
  amount: string;
  rate: string;
  goods: Array<{
    treasureId: string;
    keyword1: string;
    keywordRate1: string;
    keyword2: string;
    keywordRate2: string;
    keyword3: string;
    keywordRate3: string;
    sku: string;
    mainPictureUrl: string;
    verticalPictureUrl: string;
  }>;

  total: string;
  orderPictureUrl: string;
  executeTime: string;
  time_range: string;
}
/* */
export function refundOrderPublic(object: refundOrderPublicInfo) {
  return internalFetch('POST')(true)(stringifPath(refundOrderPublicPATH), {
    body: { ...object }
  });
}
/**/
export function freeOrderPublic(object: freeOrderPublicInfo) {
  return internalFetch('POST')(true)(stringifPath(freeOrderPublicPATH), {
    body: { ...object }
  });
}
/**/
export function setUploadImg(files: any) {
  let formData = new FormData();
  formData.append('files', files);
  return fetch(stringifPath(UPLOADPATH), {
    method: 'POST',
    body: formData
  }).then(response => response.json());
}



