import Vue from 'vue';
import Router from 'vue-router';
import Login from '@/views/Login.vue';
import Task from '@/views/Task.vue';
import Home from '@/views/Home.vue';
import PublicRefund from '@/views/PublicRefund.vue';
import PublicFree from '@/views/PublicFree.vue';
import listRefund from '@/views/ListRefund.vue';
import listFree from '@/views/ListFree.vue';
import User from '@/views/User.vue';
import { hasLogin, saveLogin } from '@/util/session';

Vue.use(Router);

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login,
      meta: { title: '' }
    },
    {
      path: '/home',
      name: 'home',
      component: Home,
      meta: { title: '', requiredAuth: true }
    },
    {
      path: '/user',
      name: 'user',
      component: User,
      meta: { title: '', requiredAuth: true }
    },
    {
      path: '/publicrefund',
      name: 'publicrefund',
      component: PublicRefund,
      meta: { title: '', requiredAuth: true }
    },
    {
      path: '/publicfree',
      name: 'publicfree',
      component: PublicFree,
      meta: { title: '', requiredAuth: true }
    },
    {
      path: '/listrefund',
      name: 'listrefund',
      component: listRefund,
      meta: { title: '', requiredAuth: true }
    },
    {
      path: '/listfree',
      name: 'listfree',
      component: listFree,
      meta: { title: '', requiredAuth: true }
    },
    {
      path: '/task',
      name: 'task',
      component: Task,
      meta: { requiredAuth: true, title: '' }
    }
  ]
});

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiredAuth)) {
    if (!hasLogin()) {
      window.location.href = '/';
    } else {
      next(); //  next()
    }
  } else {
    next(); //  next()
  }
  /* title */
  if (to.meta.title) {
    document.title = to.meta.title;
  }
});

export default router;



